main.reset {
    height: 100vh;
    width: 100%;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  main.reset > form {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 4rem;
    width: 100%;
  }
  main.reset > form > main {
    width: 480px;
    height: 421px;
    gap: 2rem;
    background: #ffffff;
    box-shadow: 0px 8px 12px rgba(9, 30, 66, 0.15),
      0px 0px 1px rgba(9, 30, 66, 0.31);
    border-radius: 8px;
    display: flex;
    flex-direction: column;
    align-items: center;
    z-index: 100;
  }
  main.reset > form > main > div:first-of-type {
    /* border: 1px solid red; */
    display: flex;
    padding-top: 2rem;
    flex-direction: column;
    align-items: center;
    gap: 1rem;
  }
  main.reset > form > main > div:nth-of-type(2) {
    width: 80%;
  
    gap: 0.5rem;
    display: flex;
    flex-direction: column;
  }
  
  main.reset > form > main > div:nth-of-type(2) > div {
    position: relative;
    width: 100%;
    height: fit-content;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    /* border: 1px solid red; */
  }
  
  main.reset > form > main > div:nth-of-type(2) > div > input,
  main.reset > form > main > div:nth-of-type(2) > div > div {
    height: 2rem !important;
    width: 100% !important;
  }
  main.reset > form > main > div:nth-of-type(2) > div > input {
    background: #ffffff;
    color: #58595d;
    border: 1px solid #dfe1e6;
    cursor: pointer;
    border-radius: 4px;
    padding-left: 0.5rem;
    outline: 0;
    /* height: 2rem; */
  }
  main.reset > form > main > div:nth-of-type(2) > div > input::placeholder {
    color: #dfe1e6;
  }
  main.reset > form > main > div:nth-of-type(2) > div > input:focus {
    border: 1px solid #0052cc;
    color: #58595d;
  }
  main.reset > form > main > div:nth-of-type(2) > div > div {
    background: #0052cc;
    mix-blend-mode: normal;
    border-radius: 4px;
  }
  main.reset > form > main > div:nth-of-type(2) > img {
    width: 1rem;
    height: 1rem;
    position: absolute;
    left: 60%;
    top: 61.2%;
    cursor: pointer;
  }
  main.reset > form > main > div:nth-of-type(2) > span {
    width: 1rem;
    height: 1rem;
    position: absolute;
    left: 60%;
    top: 54.2%;
    cursor: pointer;
    color: rgb(5, 189, 5);
  }
  main.reset > form > main > div:nth-of-type(3) {
    width: 80%;
    height: fit-content;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 2rem !important;
  }
  main.reset > form > main > div:nth-of-type(3) > div {
    width: 100% !important;
    height: 0.1rem !important;
    background: #dfe1e6 !important;
  }
  main.reset > form > main > div:nth-of-type(3) > a {
    font-style: normal;
    font-weight: bold;
    font-size: 14px;
    line-height: 20px;
    text-decoration: none;
    color: #0052cc;
  }
  main.reset > img {
    width: 100%;
    position: absolute;
    top: 48%;
  }
  
  @media screen and (max-width: 768px) {
    main.reset > img {
      width: 100%;
      position: absolute;
      top: 100%;
    }
    main.reset > form > main > div:nth-of-type(2) > span {
      left: 82%;
      top: 55.2%;
    }
    main.reset > form > main > div:nth-of-type(2) > img {
      left: 70%;
      top: 62.2%;
    }
  }
  @media screen and (min-width: 769px) and (max-width: 1300px) {
    main.reset > img {
      display: none;
    }
  }
  @media screen and (max-width: 900px) {
    main.reset > form > main > div:nth-of-type(2) > span {
      left: 68%;
      top: 54.2%;
    }
    main.reset > form > main > div:nth-of-type(2) > img {
      left: 70%;
      top: 62.2%;
    }
    #cp {
      top: 69.2% !important;
    }
  }
  @media screen and (max-width: 483px) {
    main.reset > form > main {
      width: 100% !important;
    }
    .reset > form > header > img {
      width: 15rem;
    }
    main.reset > form > main > div:nth-of-type(2) > span {
      left: 82%;
      top: 57.2%;
    }
    main.reset > form > main > div:nth-of-type(2) > img {
      left: 80%;
      top: 61.2%;
    }
    #cp {
      top: 69.2% !important;
    }
  }
  #pa {
    top: 51.2%;
  }
  #cp {
    top: 67.2%;
  }
  