@import url('https://fonts.googleapis.com/css2?family=Readex+Pro:wght@451&display=swap');


.section-head{
  font-family: 'Readex Pro', sans-serif;
}
.section-head {
    display: flex;
    gap: 1rem;
    flex-direction: column;
    height: fit-content;
    width: 25rem;
  }
  .section-head  h1 {
    font-style: normal;
    font-weight: bold;
    font-size: 21px;
    line-height: 135%;
    color: #091e42;
  }
  .section-head h2 {
    font-style: normal;
    font-weight: bold;
    font-size: 48px;
    line-height: 135%;
    color: #091e42;
  }
  .section-head p {
    font-style: normal;
    font-weight: 450;
    font-size: 16px;
    line-height: 24px;
    color: #5e6c84;
  }
  